import { 
  BigScreen, 
  getGlobalStyle, 
  SmallScreen 
} from '../../../../helpers'
import { Icon, Text } from '../../../atoms'
import { Column, Container, Row } from '../../../layout'
import { ShabitName } from '../BenefitsAlvi'
import { starsCountByShabitNameMap } from '../helpers'
import { getBenefitsTabColorByName, getBenefitsTabIconByStatus } from './helpers'

export interface BenefitsTabProps {
  name: ShabitName;
  selected?: boolean;
  status?: BenefitsTabStatus;
  title: string;
  onClick?: () => void;
}

export type BenefitsTabStatus = 'active' | 'locked' | 'completed';

export const BenefitsTab = ({
  name,
  selected,
  status = 'locked',
  title,
  onClick
}: BenefitsTabProps) => {

  const starsCount = starsCountByShabitNameMap[name]
  const textColor = selected ? getGlobalStyle('--color-base-black') : getGlobalStyle('--color-neutral-gray-silver')
  const color = selected ? getBenefitsTabColorByName(name) : getGlobalStyle('--color-neutral-gray-silver')
  const colorIcon = status !== 'locked' || selected ? getBenefitsTabColorByName(name) : getGlobalStyle('--color-neutral-gray-silver')
  const iconName = getBenefitsTabIconByStatus(status)

  return (
    <>
      <Column 
        alignItems='center' 
        clickable='pointer'
        justifyContent='center'
        onClick={onClick}
        padding='0 1px'
      >
        <Container
          alignItems='center'
          clickable='pointer'
          customHeight='30px'
          justifyContent='center'
          width='30px'
        >
          <Icon 
            clickable='pointer'
            color={colorIcon}
            name={iconName}
            sizes='md'
          />
        </Container>
        <Row
          clickable='pointer'
          justifyContent='center'
          margin='8px 0 0 0'
        >
          {[...Array(starsCount)].map((index) => {return (
            <Icon
              clickable='pointer'
              color={color}
              key={`${index}`}
              name='Star'
            />
          )})}
        </Row>
        <Row
          clickable='pointer'
          justifyContent='center'
          margin='8px 0 0 0'
        >
          <Text
            clickable='pointer'
            customColor={textColor}
            fontFamily={getGlobalStyle('--font-family-montserrat')}
            fontSize='md'
            fontWeight='semibold'
            letterSpacing='1.25px'
            textTransform='uppercase'
          >
            {title}
          </Text>
        </Row>
        <BigScreen>
          <Container 
            backgroundColor={color}
            customHeight='3px'
            margin='4px 0 0 0'
          />         
        </BigScreen>
        <SmallScreen>
          <Container 
            backgroundColor={color}
            customHeight='3px'
            margin='16px 0 0 0'
          />
        </SmallScreen>
      </Column>
    </>
  )
}