import { getGlobalStyle } from '../../../../helpers'
import { ShabitName } from '../BenefitsAlvi'

export const starsCountByShabitNameMap : Record<ShabitName, number> = {
  bronze: 1,
  silver: 2,
  gold: 3,
  vip: 4
}

export const shabitTitleByShabitNameMap : Record<ShabitName, string> = {
  'bronze': 'bronce',
  'silver': 'plata',
  'gold': 'oro',
  'vip': 'vip'
}

export const getShabitColorByName = (name: ShabitName) : string => {
  return getGlobalStyle(`--color-alvi-shabits-${name}-solid`)
}