import { useMemo } from 'react'
import { Column, Row } from '../../layout'
import { BenefitsCategory, BenefitsCategoryProps } from './BenefitsCategory'
import { BenefitsTab } from './BenefitsTab'
import { getBenefitsTabStatusByIndex } from './BenefitsTab/helpers'

export interface BenefitsAlviProps {
  shabits: Shabit[];
  active?: ShabitName;
  selected?: ShabitName;
  onChangeSelected?: (name: ShabitName) => void;
}

export type ShabitName = 'bronze' | 'silver' | 'gold' | 'vip' | string;

interface Shabit {
  title: string;
  name: ShabitName;
  categories: BenefitsCategoryProps[];
}

export const BenefitsAlvi = ({
  shabits,
  active,
  selected,
  onChangeSelected
}: BenefitsAlviProps) : React.ReactElement<BenefitsAlviProps> => {

  const activeIndex = useMemo(() => {
    return shabits.findIndex((shabit) => {return shabit.name === active})
  }, [active, shabits])

  const selectedIndex = useMemo(() => {
    return shabits.findIndex((shabit) => {return shabit.name === selected})
  }, [selected, shabits])

  const handleChangeSelected = (name: ShabitName) => {
    onChangeSelected?.(name)
  }

  return (
    <Column>
      <Row>
        {shabits.map((shabit, index) => {
          return (
            <BenefitsTab 
              {...shabit}
              key={shabit.name}
              onClick={() => {handleChangeSelected(shabit.name)}}
              selected={index === selectedIndex}
              status={getBenefitsTabStatusByIndex(index, activeIndex)}
            />
          ) 
        })}
      </Row>
      {shabits.map((shabit, index) => {
        if (index !== selectedIndex) return null

        return (
          <Column
            gap='24px'
            key={shabit.name}
            margin='24px 0 0 0'
          >
            {shabit.categories.map((category) => {
              return (
                <BenefitsCategory {...category} key={`${category.title}`} />
              )
            })}
          </Column>
        )
      })}
    </Column>
  )
}