import { BenefitsTabStatus } from '..'
import { IconProps } from '../../../../atoms'
import { ShabitName } from '../../BenefitsAlvi'
import { getShabitColorByName } from '../../helpers'

export const getBenefitsTabIconByStatus = (status: BenefitsTabStatus) : IconProps['name'] => {
  switch (status) {
    case 'active':
      return 'UserCircle'
    case 'completed':
      return 'CheckCircle'
    case 'locked':
      return 'LockCircle'
    default:
      return 'UserCircle'
  }
}

export const getBenefitsTabColorByName = (name: ShabitName) : string => {
  return getShabitColorByName(name)
}

export const getBenefitsTabStatusByIndex = (index: number, activeIndex: number) : BenefitsTabStatus => {
  if (index === activeIndex) return 'active'
  if (index < activeIndex) return 'completed'
  return 'locked'
}